<template>
  <div>
    <Loader v-show="!loaded" />
    <div id="map" v-if="loaded">    
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
      </v-map>    
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import router from '../router'
import Loader from './Loader'

export default {
  data () {
    return {
      geojson: [],
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      loaded: false
    }
  },
  created () {
    this.createGeo()
  },
  methods: {
    async createGeo () {
      const res = await import(`../json/continents.json`)
      this.geojson = [...res.features]
      this.loaded = await true
    },
    onEachFeature (feature, layer) {
      layer.setStyle({fillColor: this.randomColor()})
      layer.on('click', () => {
        router.push({ 
          name: 'LearnSubject', 
          params: { 
            name: layer.feature.properties.continent.replace(/ /g, '').toLowerCase(), 
          }
        })
      })
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader
  },
}
</script>
<style scoped>
@import "~leaflet/dist/leaflet.css";
.fullHeight {
  min-height: 100vh;
}
.leaflet-container {
  background-color: #A3CCFF;
}
</style>


